<template>
    <div id="app">
        <div class="window_con">
            <div class="title">
                <div>学生答题进度</div>
            </div>
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div>
            <div class="btnGroup">
                <div class="statusTip">
                    <div class="tip"><img src="@/assets/status1.png" alt="">已提交</div>
                    <div class="tip"><img src="@/assets/status2.png" alt="">电量过低</div>
                    <!-- <div class="tip"><img src="@/assets/status3.png" alt="">试卷未插入</div> -->
                    <div class="tip"><img src="@/assets/status4.png" alt="">试卷错误</div>
                    <div class="tip"><img src="@/assets/status5.png" alt="">设备离线</div>
                </div>
                <div class="operate" v-if="!isInner">
                    <div class="finish" @click="statusStoped" v-if="!finished">
                        <img src="@/assets/icon_shou.png" />收卷</div>
                    <div class="finish" @click="statusFinished" v-if="finished">
                        <img src="@/assets/icon_end@2x.png" />结束</div>
                </div>
            </div>
            <div class="stuList" v-if="baseInfo">
                <vue-scroll>
                    <div class="group finish">
                        <div class="tit">已批改：{{ baseInfo.correctStu.length }}人</div>
                        <div class="stuGroup">
                            <ul>
                                <li class="stuItem" v-for="(stu,index) in baseInfo.correctStu"
                                    v-if="baseInfo.correctStu.length">
                                    <div class="avatar">
                                        <img :src="stu.headUrl" />
                                    </div>
                                    <div class="name">{{stu.name}}</div>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="group writing">
                        <div class="tit">作答中：{{ baseInfo.writingStu.length }}人</div>
                        <div class="stuGroup">
                            <ul>
                                <li class="stuItem" v-for="(stu,index) in baseInfo.writingStu"
                                    v-if="baseInfo.writingStu.length">
                                    <div class="avatar">
                                        <img :src="stu.headUrl" />
                                        <el-progress type="circle" :percentage="stu.progress" :stroke-width=" 2*dpr"
                                            :show-text="false" :width="dpr>0.54?dpr*50:dpr*70" color="#00AAFF"></el-progress>
                                        <div class="status">
                                            <img v-if="stu.status==1" src="@/assets/status1.png" alt="">
                                        </div>
                                    </div>
                                    <div class="name">{{stu.name}}</div>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="group error">
                        <div class="tit">异常：{{ baseInfo.anomalyStu.length }}人</div>
                        <div class="stuGroup">
                            <ul>
                                <li class="stuItem" v-for="(stu,index) in baseInfo.anomalyStu"
                                    v-if="baseInfo.anomalyStu.length">
                                    <div class="avatar" :class="{offline:stu.status==5}">
                                        <img :src="stu.headUrl"/>
                                        <el-progress type="circle" :percentage="stu.progress" :stroke-width=" 2*dpr"
                                            :show-text="false" :width="dpr*50" color="#00AAFF"></el-progress>
                                        <div class="status">
                                            <img v-if="stu.status==1" src="@/assets/status1.png" alt="">
                                            <img v-if="stu.status==2" src="@/assets/status2.png" alt="">
                                            <img v-if="stu.status==3" src="@/assets/status3.png" alt="">
                                            <img v-if="stu.status==4" src="@/assets/status4.png" alt="">
                                            <img v-if="stu.status==5" src="@/assets/status5.png" alt="">
                                        </div>
                                    </div>
                                    <div class="name">{{stu.name}}</div>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="group absent">
                        <div class="tit">缺席：{{ baseInfo.absentStu.length }}人</div>
                        <div class="stuGroup">
                            <ul>
                                <li class="stuItem" v-for="(stu,index) in baseInfo.absentStu"
                                    v-if="baseInfo.absentStu.length">
                                    <div class="avatar">
                                        <img :src="stu.headUrl" />
                                    </div>
                                    <div class="name">{{stu.name}}</div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </vue-scroll>
            </div>
        </div>
    </div>
</template>
<script>
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'

    export default {
        name: 'App',
        data() {
            return {
                dpr: 1,
                isInner: false,
                finished: false,
                baseInfo: null
            }
        },
        mounted() {
            this.dpr = localStorage.getItem("width") / 1920
            console.log(this.dpr)
            this.finished = utils.getUrlPrams("finished") == "true" ? true : false
            this.isInner = utils.getUrlPrams("isInner") == "true" ? true : false

            //注册需要外部调用的方法
            window.setProgress = this.setProgress
            window.setSubmit = this.setSubmit
            window.changeGroup = this.changeGroup
            window.setBaseData = this.setBaseData
            this.setBaseData()
        },
        methods: {
            setBaseData() {
                let that = this
                // let baseInfo = {"absentStu":[{"name":"高春涛啊","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":20,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0},{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":0,"status":0}],"anomalyStu":[],"writingStu":[{"name":"高春涛","stuId":17310,"headUrl":"http://res.mgboard.com/avatar/online.png","stuMac":"1B2A00000CAB","progress":30,"status":0}],"correctStu":[]}
                // that.baseInfo = baseInfo
                window.cefQuery({
                    request: "setBaseData",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                        let baseInfo = JSON.parse(response)
                        that.baseInfo = baseInfo
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            setProgress(stuId, progress) {
                for (let i = 0; i < this.baseInfo.writingStu.length; i++) {
                    if (this.baseInfo.writingStu[i].stuId === stuId) {
                        this.$set(this.baseInfo.writingStu[i], "progress", progress)
                    }
                }
                for (let i = 0; i < this.baseInfo.anomalyStu.length; i++) {
                    if (this.baseInfo.anomalyStu[i].stuId === stuId) {
                        this.$set(this.baseInfo.anomalyStu[i], "progress", progress)
                    }
                }
            },
            /*
                @type: 要更改成为的类型  
                1=已批改 2=作答中 3=异常 4=缺席
                @status: 要更改成为的状态
                0=正常 1=提交 2=低电量 3=未插入试卷 4=试卷错误 5离线
            */
            changeGroup(stuId, type, status) {
                for (let i in this.baseInfo.writingStu) {
                    console.log(this.baseInfo.writingStu[i]);
                    if (this.baseInfo.writingStu[i].stuId === stuId) {
                        this.$set(this.baseInfo.writingStu[i], "status", status)
                        if (type != 2) {
                            let stu = this.baseInfo.writingStu[i]
                            this.$delete(this.baseInfo.writingStu, i)
                            this.pushStu(stu, type)
                        }
                        return
                    }
                };
                for (let i in this.baseInfo.absentStu) {
                    console.log(this.baseInfo.absentStu[i]);
                    if (this.baseInfo.absentStu[i].stuId === stuId) {
                        this.$set(this.baseInfo.absentStu[i], "status", status)
                        if (type != 4) {
                            let stu = this.baseInfo.absentStu[i]
                            this.$delete(this.baseInfo.absentStu, i)
                            this.pushStu(stu, type)
                        }
                        return
                    }
                };
                for (let i in this.baseInfo.anomalyStu) {
                    console.log(this.baseInfo.anomalyStu[i]);
                    if (this.baseInfo.anomalyStu[i].stuId === stuId) {
                        this.$set(this.baseInfo.anomalyStu[i], "status", status)
                        if (type != 3) {
                            let stu = this.baseInfo.anomalyStu[i]
                            this.$delete(this.baseInfo.anomalyStu, i)
                            this.pushStu(stu, type)
                        }

                        return
                    }

                };
                for (let i in this.baseInfo.correctStu) {
                    console.log(this.baseInfo.correctStu[i]);
                    if (this.baseInfo.correctStu[i].stuId === stuId) {
                        this.$set(this.baseInfo.correctStu[i], "status", status)
                        if (type != 1) {
                            let stu = this.baseInfo.correctStu[i]
                            this.$delete(this.baseInfo.correctStu, i)
                            this.pushStu(stu, type)
                        }
                        return
                    }
                };
            },
            pushStu(stu, type) {
                if (type == 1) {
                    this.baseInfo.correctStu.push(stu)
                } else if (type == 2) {
                    this.baseInfo.writingStu.push(stu)
                } else if (type == 3) {
                    this.baseInfo.anomalyStu.push(stu)
                } else if (type == 4) {
                    this.baseInfo.absentStu.push(stu)
                }
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowAnswerProgress",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            statusFinished() {
                console.log("statusFinished")
                window.cefQuery({
                    request: "statusFinished",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });

            },
            statusStoped() {
                console.log("statusStoped")
                this.finished = true
                window.cefQuery({
                    request: "statusStoped",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    li {
        list-style: none;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #333;
    }

    .stuItem path:first-of-type {
        stroke: rgba(0, 0, 0, 0) !important;
    }
</style>
<style scoped>
    .window_con {
        width: 100%;
        height: 100vh;
        border-radius: 0.2rem;
        background: #fff;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
    }

    .stuList {
        box-sizing: border-box;
        height: calc(100vh - 1rem);
        padding: 0 0.175rem 0.8rem 0.175rem;
        text-align: left;
    }

    .stuList .stuGroup {
        min-height: 1rem;
    }

    .btnGroup {
        box-sizing: border-box;
        width: 100%;
        height: 0.80rem;
        padding-top: 0.15rem;
        background: #fff;
        position: absolute;
        left: 0;
        bottom: 0;
        text-align: right;
        z-index: 9;
    }

    .btnGroup .statusTip{
        float: left;
        padding-left: 0.3rem;
    }

    .btnGroup .operate{
        float: right;
    }

    .btnGroup .statusTip .tip{
        display: inline-block;
        font-size: 0.15rem;
        color: #2E4A66;
        line-height: 1.8;
        margin-right: 0.2rem;
    }
    .btnGroup .statusTip .tip img{
        display: inline-block;
        width: 0.15rem;
        height: 0.15rem;
        padding-bottom: 0.03rem;
        vertical-align: middle;
    }

    .btnGroup .operate>div{
        box-sizing: border-box;
        display: inline-block;
        font-size: 0.12rem;
        line-height: 1.8;
        color: #526F8B;
        padding-right: 0.3rem;
    }

    .btnGroup .finish.finished {
        opacity: 0.3;
    }

    .btnGroup img {
        width: 0.27rem;
        height: 0.27rem;
        display: inline-block;
        vertical-align: middle;
        margin-right: 0.05rem;
    }

    .btnGroup .close {
        color: #526F8B;
        cursor: pointer;
    }

    .stuList .group .avatar.offline {
        opacity: 0.3;
    }

    .stuList .group .tit {
        font-size: 0.15rem;
        font-weight: bold;
        line-height: 1;
        padding-left: 0.125rem;
        color: #2E4A66;
        padding-top: 0.5rem;
    }

    .stuList .group:first-of-type .tit {
        padding-top: 0;
    }

    .stuItem {
        display: inline-block;
        width: 0.75rem;
        padding-top: 0.2rem;
        text-align: center;
        translate: all 0.2s linear;
    }



    .stuItem .avatar {
        position: relative;
        width: 0.5rem;
        height: 0.5rem;
        margin: 0 auto;
        transition: all 0.3s linear;
        animation: 0.3s bounceIn;
    }

    .stuItem .avatar img {
        width: 0.5rem;
        height: 0.5rem;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top: 0;
    }

    .stuItem .avatar .status {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 0.15rem;
        height: 0.15rem;
        border-radius: 50%;
    }

    .stuItem .avatar .status img {
        display: block;
        width: 100%;
        height: 100%;
    }

    .stuItem .name {
        font-size: 0.15rem;
        color: #2E4A66;
        line-height: 1.2;
        padding: 0.1rem 0 0 0;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .animationed {
        display: block !important;
        animation: bounceIn 1s linear;
    }

    .fadeOut {
        animation: fadeOut 1s linear;
    }

    @keyframes fadeOut {
        0% {
            opacity: 1;
            width: 50px
        }

        20% {
            opacity: 0;
            width: 50px;
        }

        100% {
            width: 0;
            padding: 0;
        }


    }

    @keyframes bounceIn {

        from,
        20%,
        40%,
        60%,
        80%,
        to {
            -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
            animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        }

        0% {
            opacity: 0;
            -webkit-transform: scale3d(.3, .3, .3);
            transform: scale3d(.3, .3, .3);
        }

        50% {
            -webkit-transform: scale3d(1.1, 1.1, 1.1);
            transform: scale3d(1.1, 1.1, 1.1);
        }

        100% {
            opacity: 1;
            -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
        }
    }
    @media screen and (max-width: 700px) {
        .stuItem{
            width: 1rem;
        }
        .stuItem .avatar{
            width: 0.7rem;
            height: 0.7rem;
        }
        .stuItem .avatar img{
            width: 0.7rem;
            height: 0.7rem;
        }
    }
</style>